function form() {
  const form = document.querySelector('.contacts__form');
  form.addEventListener('submit', (e) => {
    e.preventDefault();

    form.addEventListener('submit', (event) => {
      event.preventDefault();

      const formData = new FormData(form); //   ,   input     name=""
      const json = JSON.stringify(Object.fromEntries(formData.entries()));
      //       ,    ,    JSON

      postData('url', json) //      
        .then((data) => { //   promise
          console.log(data); //   
          showThanks('!  !');
        })
        .catch(() => { //  reject ()
          showThanks('-   ...');
        })
        .finally(() => { //    
          form.reset(); //  
        });
    });

    async function postData(url, data) { //      
      // const result = await fetch(url, { // await    fetch
      //   method: 'POST', // POST  , GET 
      //   headers: {'Content-type': 'application/json'}, //    JSON,     formData,   
      //   body: data, //  ,   GET,   
      // });
      // return await result.json(); //      PROMISE   JSON
      const result = new Promise((resolve, reject) => { // 
        setTimeout(() => {
          resolve(data);
        }, 300);
      });
      return await result;
    }

    function showThanks(text) { //    
      alert(`${text}`);
    }


  });



}

export default form;

